<template>
    
    <li class="hasSub highlight-menu">
        <a  :class="[children_show?'expand':'notExpand',{'active':active}]"   @click.prevent="handleSelect"><span class="txt">{{data.name}}</span>
	    <i v-if="data.children" class="l-arrows-right sideNav-arrow" :class="children_show?'rotate90':''"></i>
        </a>
	
	
        <ul v-if="data.children"  class="sub" :class="{'show':children_show}">
    
	    <Menu-itemto
		    v-for="(item, i) in data.children"
		    :key="i"
		    :data="item">
	    </Menu-itemto>
	    
        </ul>
    </li>
    
</template>


<script>
    import Emitter from '../../mixins/emitter'
    import menuItemto from './menu-itemto.vue'
    const prefixCls = 'am-menu-item';
    
    
    export default {
        name: 'menu-item',
        mixins: [ Emitter ],
	components: {menuItemto},
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        data () {
            return {
                prefixCls: prefixCls,
                active:false,
		children_show:false
            };
        },
        methods:{
            handleSelect(){
                if(this.data.children){
		    this.children_show = !this.children_show;
		}else{
		    
		    //这里打开连接
		    if(this.data.url){
			this.$amui.$emit('loadURL',this.data.url);
		    }
		    
		    this.active = true;//激活当前菜单
		    this.$amui.$emit('cancelAll-active',[this._uid,this.$parent._uid]);//取消所有菜单激活
		}
		//todo:折叠所有子菜单
		this.$amui.$emit('menu-item-expand',[this._uid]);
            }
        },
        mounted () {
            var _this = this;
	    this.$amui.$on('menu-item-expand',function(arr){
		if(arr.contains(_this._uid))return;
		_this.children_show = false;
	    });
	    
            this.$amui.$on('cancelAll-active',function(arr){
                if(arr.contains(_this._uid)){
                    _this.active = true;
		    return;
		};
                _this.active = false;
            });
        }
    }
</script>





