<template>
    <!-- todo: page-content -->
    <div class="page-content right-sidebar-page clearfix sidebar-page" style="position: relative"  :style="{'margin-right':lsize.right+'px','margin-left':lsize.left+'px'}">
    
    
    
        
        
        <!-- .page-content-wrapper -->
        
        <div id="page-content-wrapper" class="page-content-wrapper">
            <!-- .page-content-inner
            
            -->
                <slot>
                    <iframe id="pageIframeContent" frameborder="none" name="pageIframeContent"  :src="contentURL"  height="100%" width="100%"  ></iframe>
                </slot>
             <!--/ .page-content-inner-->
        </div>
         <!--/ page-content-wrapper-->
    </div>
    <!-- / page-content -->
    
</template>

<script>
    import   '@components/amui/utils/iframeResizer.js'
    export default {
        name: 'LayoutContent',
        props: {
            size:{
                type:Object,
                default () {
                    return {
                        left:210,
                        right:210
                    };
                }
            },
            debug:{
                type:Boolean,
                default:true
            },
            url:{
                type:String,
                default:'http://localhost:9000/pages/content.html'
            }
        },
        data () {
            return {
                lsize:{
                    left:this.size.left,
                    right:this.size.right
                },
                contentURL:this.url
            }
        },
        methods:{
            screenChange:function(type){
                if(type == 'phone'){
                    this.lsize.left = 0;
                    this.lsize.right = 0;
                }else if(type == 'tablet'){
                    this.lsize.left = 60;
                    this.lsize.right = 0;
                }else if(type == 'laptop'){
                    this.lsize.right = 0;
                    this.lsize.left = this.size.left;
                }else if(type == 'large'){
                    this.lsize.right = this.size.right;
                    this.lsize.left = this.size.left;
                }
            },
            openMenuEvent:function(type){
                var screen = this.$amui.getBreakpoint();
                if(type == "open"){
                    
                    if(screen == 'phone'){
                        this.lsize.left =0;
                    }else {
                        this.lsize.left =this.size.left;
                    }
                }else{
                    if(screen == 'phone'){
                        this.lsize.left =0;
                    }else{
                        this.lsize.left =60;
                    }
                }
            },
            openSidebarEvent:function (type) {
                var screen = this.$amui.getBreakpoint();
                if(type == "open"){
                    if(screen == 'large'){
                        this.lsize.right =this.size.left;
                    }
                }else{
                    this.lsize.right =0;
                }
            },
            //Event pai on PageContent changed
            sendEventContentbeyond:function(height){
                //右侧窗口事件
                this.$amui.$emit('contentBeyond',{
                    contentHeight:height
                });
            },
            loadIframe:function(){
                return;
                var _this = this;
                var iframe = document.getElementById('pageIframeContent');
                if (iframe.attachEvent){
                    iframe.attachEvent("onload", function(){
                        _this.setIframeContent();
                    });
                } else {
                    iframe.onload = function(){
                        _this.setIframeContent();
                    };
                }
            },//Iframe loaded complete
            setIframeContent:function(){
                return;
                var _this = this;
                $('#pageIframeContent').iFrameResize({
                    
//                iFrameResize({
                    log                     : false,                  // Enable console logging
                    inPageLinks             : false,
                    autoResize:true,
                    resizedCallback : function(messageData){ // Callback fn when resize is received
                        _this.sendEventContentbeyond(messageData.height);
                        $('p#callback').html(
                                '<b>Frame ID:</b> '    + messageData.iframe.id +
                                ' <b>Height:</b> '     + messageData.height +
                                ' <b>Width:</b> '      + messageData.width +
                                ' <b>Event type:</b> ' + messageData.type
                        );
                    },
                    messageCallback : function(messageData){ // Callback fn when message is received
                        $('p#callback').html(
                                '<b>Frame ID:</b> '    + messageData.iframe.id +
                                ' <b>Message:</b> '    + messageData.message
                        );
                        document.getElementsByTagName('iframe')[0].iFrameResizer.sendMessage('Hello back from parent page');
                    },
                    closedCallback : function(id){ // Callback fn when iFrame is closed
                        $('p#callback').html(
                                '<b>IFrame (</b>'    + id +
                                '<b>) removed from page.</b>'
                        );
                    }
                });
            },
            loadURL:function(url){
                this.contentURL = url;
            }
        },
        mounted () {
            var _this = this;
            //屏幕改变事件
            this.$amui.$on('screenChange',function(type){
                _this.screenChange(type);
            });
            this.screenChange(this.$amui.getBreakpoint());
            //打开Menu事件
            this.$amui.$on('openMenuEvent',function(type){
                _this.openMenuEvent(type);
            });
            //右侧窗口事件
            this.$amui.$on('openSidebarEvent',function(type){
               _this.openSidebarEvent(type);
            });
    
            //右侧窗口事件
            this.$amui.$on('loadURL',function(url){
                _this.loadURL(url);
            });
            
            if(this.contentURL){
                this.loadIframe();
            }
            
        }
    }
</script>





