<template>
    
    <li>
        <a @click.prevent="handleSelect"  :class="{'active':active}"><span class="txt">Basic forms</span></a>
    </li>
    
    
</template>


<script>
    import Emitter from '../../mixins/emitter'
    const prefixCls = 'am-menu-item';
    
    
    export default {
        name: 'menu-itemto',
        mixins: [ Emitter ],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        data () {
            return {
                prefixCls: prefixCls,
                active:false
            };
        },
        methods:{
            handleSelect(){
                    this.active = true;//激活当前菜单
                    this.$amui.$emit('cancelAll-active',[this._uid,this.$parent._uid,this.$parent.$parent._uid]);//取消所有菜单激活
            }
        },
        mounted () {
            var _this = this;
            this.$amui.$on('cancelAll-active',function(arr){
                if(arr.contains(_this._uid)){
                    _this.active= true;
                    return;
                };
                _this.active = false;
            });
            var _this = this;
            this.$amui.$on('menu-expand',function(arr){
                if(arr.contains(_this._uid))return;
                _this.children_show = false;
            });
        }
    }
    
</script>





