<template>
    <div id="header" class="page-navbar  header-fixed">
        <!-- .navbar-brand -->
        <a :href="url" class="navbar-brand" :class="{'collapse-sidebar':isCollapse,'no-collapse-sidebar':!isCollapse}">
            <div class="logo hidden-xs">
                <img :src="logo" :alt="title" style="height:50px;width:50px;"> <!-- 50x 50 -->
                <span style="font-size: 16px;
                    display: inline-block;
                    position: relative;
                    color:#000;">{{title}}</span>
            </div>
            <img :src="logosm" class="logo-sm hidden-lg hidden-md" :alt="title"> <!-- 50x 50 -->
        </a>
        <!-- / navbar-brand -->
        
        <!-- .no-collapse -->
        <div id="navbar-no-collapse" class="navbar-no-collapse">
    
            <!-- top left nav -->
            <ul class="nav navbar-nav">
                
                <li class="toggle-sidebar" @click.prevent="collapseNavbar">
                    <a href="#">
                        <i class="fa fa-reorder"></i>
                        <span class="sr-only">折叠菜单</span>
                    </a>
                </li>
                
                <!-- 左侧按钮插槽 -->
                <slot name='left-bar'></slot>
                
            </ul>
            <!-- / top left nav -->
    
            <!-- 右侧按钮插槽 -->
            <ul class="nav navbar-nav navbar-right">
                <slot>
                </slot>
                <li v-if="rightWindow" @click.prevent="toggleSidebar">
                    <a id="toggle-right-sidebar" href="#" class="tipB" title="" data-original-title="Toggle right sidebar">
                        <i class="l-software-layout-sidebar-right"></i>
                        <span class="sr-only">Toggle right sidebar</span>
                    </a>
                </li>
            </ul>
            
            <!-- / top right nav -->
        </div>
        <!-- / collapse -->
    </div>
</template>
<script>
    import logo from '@components/amui/assets/img/logo.png'
    import logosm from '@components/amui/assets/img/logosm.png'
    
    const prefixCls = 'amui-layout';
    export default {
        name: 'LayoutHeader',
        props: {
            logo: {
                type: String,
                default: logo
            },
            logosm: {
                type: String,
                default: logosm
            },
            url:{
                type:String,
                default:'/index.html'
            },
            title:{
                type:String,
                default:'首页'
            },
            rightWindow:{
                type:Boolean,
                default:false
            }
        },
        data () {
            return {
                isCollapse:false,
                isRightWindw:true
            }
        },
        methods:{
            collapseNavbar:function(e){
                this.isCollapse = !this.isCollapse;
		if(this.isCollapse){
		    this.$amui.$emit('openMenuEvent','close');
		}else{
		    this.$amui.$emit('openMenuEvent','open');
		}
            },
            toggleSidebar:function(){
                this.isRightWindw = !this.isRightWindw;
                if(this.isRightWindw){
                    this.$amui.$emit('openSidebarEvent','open');
                }else{
                    this.$amui.$emit('openSidebarEvent','close');
                }
            },
            screenChange:function(type){
                var screen = this.$amui.getBreakpoint();
                if(type == "phone"){
                    this.isCollapse = true;
                    this.isRightWindw = false;
                }else if(type == "tablet"){
                    this.isCollapse = true;
                    this.isRightWindw = false;
                }else if(type == "laptop"){
                    this.isCollapse = false;
                    this.isRightWindw = false;
                }else if(type == "large"){
                    this.isCollapse = false;
                    this.isRightWindw = true;
                }
            }
        },
        mounted () {
            var _this = this;
            this.$amui.$on('screenChange',function(type){
                _this.screenChange(type);
            });
            this.screenChange(this.$amui.getBreakpoint());
    
           
            
        }
    };
</script>


