<template>
    <!-- todo: footer -->
    <div id="footer" class="clearfix sidebar-page right-sidebar-page"
         :style="{'margin-left':lsize.left+'px','margin-right':lsize.right+'px'}">
        <!-- Start #footer  -->
        <p class="pull-left">
            版权所有 ©2017 <a href="http://www.automic.com.cn/" class="color-blue strong" target="_blank">AMUI</a>. All rights reserved.
        </p>
        <p class="pull-right">
            <a href="http://www.automic.com.cn/about/introduction/" class="mr5">企业介绍</a>
            |
            <a href="http://www.automic.com.cn/#services" class="ml5 mr25">关于我们</a>
        </p>
    </div>
    <!-- / footer -->
</template>

<script>
    export default {
        name: 'LayoutFooter',
        props: {
            size:{
                type:Object,
                default () {
                    return {
                        left:210,
                        right:210
                    };
                }
            },
        },
        data () {
            return {
                lsize:{
                    left:this.size.left,
                    right:this.size.right
                }
            }
        },
        methods:{
            screenChange:function(type){
                console.log(type);
                if(type == 'phone'){
                    this.lsize.left = 0;
                    this.lsize.right = 0;
                }else if(type == 'tablet'){
                    this.lsize.left = 60;
                    this.lsize.right = 0;
                }else if(type == 'laptop'){
                    this.lsize.right = 0;
                    this.lsize.left = this.size.left;
                }else if(type == 'large'){
                    this.lsize.right = this.size.right;
                    this.lsize.left = this.size.left;
                }
            },
            openMenuEvent:function(type){
                var screen = this.$amui.getBreakpoint();
                if(type == "open"){
                    
                    if(screen == 'phone'){
                        this.lsize.left = 0;
                    }else{
                        this.lsize.left =this.size.left;
                    }
                }else{
                    
                    if(screen == 'phone'){
                        this.lsize.left = 0;
                    }else{
                        this.lsize.left =60;
                    }
                }
            },
            openSidebarEvent:function (type) {
                var screen = this.$amui.getBreakpoint();
                if(type == "open"){
                    if(screen == 'large'){
                        this.lsize.right =this.size.left;
                    }
                }else{
                    this.lsize.right =0;
                }
            }
        },
        mounted () {
            var _this = this;
            //屏幕改变事件
            this.$amui.$on('screenChange',function(type){
                _this.screenChange(type);
            });
            this.screenChange(this.$amui.getBreakpoint());
            //打开Menu事件
            this.$amui.$on('openMenuEvent',function(type){
                _this.openMenuEvent(type);
            });
            //右侧窗口事件
            this.$amui.$on('openSidebarEvent',function(type){
                _this.openSidebarEvent(type);
            });
            
        }
    }
</script>





