

export default {
        name: 'app',
        data(){
                return {
                        normal:true
                }
        },
        props: {
                id: {
                        type: String,
                        default: 'app-am'
                },
                theme: String
        },

        mounted () {
                //this.$amui.theme = this.theme
                var _this = this;
                this.$amui.$on('contentBeyond',function(obj){
                        _this.onContentBeyond(obj.contentHeight);
                });
        },
        computed: {
                classes () {
                        return {
                                ['theme--'+this.theme]: true,
                                ['normal']:this.normal
                        }
                }
        },
        methods:{
                onContentBeyond:function(height){
                        if(height  > (document.documentElement.clientHeight - 100)){
                                this.normal = false;
                        }else{
                                this.normal = true;
                        }
                }
        },
        watch: {
                theme () {
                        //this.$amui.theme = this.theme
                }
        },

        render (h) {
                const data = {
                        staticClass: 'application',
                        'class': this.classes,
                        attrs: {'data-app': true},
                        domProps: {id: this.id}
                }

                return h('div', data, this.$slots.default)
        }
}



