<template>
    <aside id="right-sidebar" class="right-sidebar" :class="{'hide-sidebar':hideSidebar,'sidebar-fixed':sidebarixed}" :style="{'width':width+'px'}">
        <div class="sidebar-inner">
            <slot>
                dd
            </slot>
        </div>
    </aside>
</template>


<script>
    import '@components/amui/assets/plugin/core/slimscroll/jquery.slimscroll.min.js'
    import '@components/amui/assets/plugin/core/slimscroll/jquery.slimscroll.horizontal.min.js'
    import Emitter from '@components/amui/mixins/emitter'
    
    
    export default {
        name:'LayoutWindow',
        mixins: [ Emitter],
        props: {
            width:{
                type:[Number,String],
                default:210
            }
        },
        data(){
            return {
                hideSidebar:false,
                sidebarixed:false
            }
        },
        methods:{
            toggleSidebar:function(type){
                if(type == 'open'){
                    this.hideSidebar = false;
                }else{
                    this.hideSidebar = true;
                }
            },
            screenChange:function(type){
                if(type == 'phone'){
                    this.hideSidebar = true;
                }else if(type == 'tablet'){
                    this.hideSidebar = true;
                }else if(type == 'laptop'){
                    this.hideSidebar = true;
                }else if(type == 'large'){
                    this.hideSidebar = false;
                }
            }
        },
        mounted () {
            var _this = this;
            $(".sidebar-inner").slimScroll({
                height: '100%',
                complate:function(){
                }
            });
            //屏幕改变事件
            this.$amui.$on('screenChange',function(type){
                _this.screenChange(type);
            });
            this.$amui.$on('openSidebarEvent',function(type){
                _this.toggleSidebar(type);
            });
            this.screenChange(this.$amui.getBreakpoint());
        }
    }
    
</script>


