<template>
    
    
    <li :class="[{'hover-li':hoverLi}]" @mouseout.capture="handleEnter" @mouseover.capture="handleOver" >
	
	<a  @click.prevent="handleSelect" :class="[children_show?'expand':'notExpand',{'active':active}]">
	    <i v-if="data.children" class="l-arrows-right sideNav-arrow" :class="children_show?'rotate90':''"></i>
	    <i :class="data.icon_class"></i>
	    <span class="txt">{{data.name}}</span>
	</a>
	
	
	<ul v-if="data.children" class="sub" :class="{'show':children_show}">
	    <Menu-item
		    v-for="(item, i) in data.children"
		    :key="i"
		    :data="item">
	    </Menu-item>
	</ul>
    </li>
    
</template>


<script>
    import Emitter from '../../mixins/emitter'
    import MenuItem from './menu-item.vue'
    const prefixCls = 'am-menu';
    
    export default {
	name: 'menuItem',
	mixins: [Emitter],
	components: {MenuItem},
	props: {
	    data: {//菜单数据
		type: Object,
		default () {
		    return {};
		}
	    },
	    index:[String,Number],//菜单index
	    collapseSidebar:[String,Number,Boolean]//打开窗口
	},
	data () {
	    return {
		prefixCls: prefixCls,
		children_show:false,//展开子菜单
		active:false,//选中子菜单或者该菜单
		hoverLi:false//悬浮展开子菜单
	    };
	},
	created:function(){
	    if(this.index == 0){
	        this.active = true;
	    }
	},
	watch:{
	    collapseSidebar:function(val, oldVal){
	        if(val){
	            this.children_show = false;
		}else{
		    if(this.active){
			this.children_show = true;
		    }
		}
	    }
	},
	methods: {
	    handleSelect:function(){
	        if(this.collapseSidebar){
	            return;
		}
	        //todo:展示当前子菜单或者打开
		if(!this.data.children){
		    if(!this.active){
			this.active = true;
		    }
		    //todo:取消所有菜单(子菜单、父菜单)激活
		    this.$amui.$emit('cancelAll-active',[this._uid]);
		}else{
		    this.children_show = !this.children_show;
		}
		//todo:折叠所有子菜单
		this.$amui.$emit('menu-expand',[this._uid]);
	    },
	    handleOver:function(){
	        this.hoverLi = true;
	    },
	    handleEnter:function(){
		this.hoverLi = false;
	    }
	},
	mounted:function(){
	    
	    var _this = this;
	    
	    this.$amui.$on('menu-expand',function(arr){
		if(arr.contains(_this._uid))return;
		_this.children_show = false;
	    });
	    
	    this.$amui.$on('cancelAll-active',function(arr){
	        if(arr.contains(_this._uid)){
	            _this.active = true;
		    return;
		};
		_this.active = false;
	    });
	}
    }

</script>



