<template>
    <li class="dropdown">
	<a href="#" data-toggle="dropdown">
	    <i :class="icon"></i>
	    <span class="sr-only">{{title}}</span>
	    <span v-if="badge" class="badge badge-danger">{{badge}}</span>
	</a>
	<ul class="dropdown-menu right dropdown-notification animated fadeIn" role="menu" style="margin-left: -90px;">
	    <li v-if="title">
		<a href="#" class="dropdown-menu-header">{{title}}</a>
	    </li>
	    <slot>
		<li v-for="item in data">
		<a href="#"><i :class="item.icon"></i> {{item.text}}</a>
	    </li>
	    </slot>
	    
	    <li v-if="footertitle">
		<a href="#" class="view-all">{{footertitle}}<i class="l-arrows-right"></i> </a>
	    </li>
	</ul>
    </li>
</template>


<script>
    export default {
	name: 'HeaderMenu',
	props: {
	    icon: {
		type: String,
		default: 'fa fa-bell-o'
	    },
	    title: {
		type: String
	    },
	    footertitle:{
		type: String
	    },
	    badge:{
	        type:[String]
	    },
	    data: {
		type: Array,
		default () {
		    return [];
		}
	    }
	    
	},
	data () {
	    return {}
	}
    }
</script>


