/**
 * Vue.use 的时候会调用install方法
 * @type {{install: ((Vue)), name: string}}
 */
// import store from 'storejs'
import JRespond from '../../utils/jRespond';


const AmUI = {
        install (Vue) {
                Vue.prototype.$amui = new Vue({
                        data: {
                                breakpoint: {},
                                theme: 'default',
                                jRes:JRespond.JRespond([])
                        },
                        methods:{
                                // setStore:function(key,value){
                                //         store(key,value);
                                // },
                                // getStore:function(key){
                                //         return store(key);
                                // },
                                setRespond:function(arr){
                                        this.jRes = JRespond.JRespond(arr);
                                },
                                addRespondFunc:function(obj){
                                        jRes.addFunc(obj);
                                },
                                getBreakpoint:function(){
                                        return this.jRes.getBreakpoint();
                                },
                                initRespond:function(){
                                        var _this = this;
                                        this.jRes = JRespond.JRespond([
                                                {
                                                        label: 'phone',//超小屏幕 手机
                                                        enter: 0,
                                                        exit: 767
                                                }, {
                                                        label: 'tablet',//小屏幕 平板 中等屏幕 桌面显示器
                                                        enter: 768,
                                                        exit: 1025
                                                }, {
                                                        label: 'laptop',
                                                        enter: 1026,
                                                        exit: 1366
                                                }, {
                                                        label: 'large',
                                                        enter: 1367,
                                                        exit: 10000
                                                }
                                        ]);
                                        this.jRes.addFunc({
                                                breakpoint: 'large',
                                                enter: function () {
                                                        _this.$emit('screenChange','large');
                                                },
                                                exit: function () {
                                                }
                                        });

                                        this.jRes.addFunc({
                                                breakpoint: 'laptop',
                                                enter: function () {
                                                        _this.$emit('screenChange','laptop');
                                                },
                                                exit: function () {
                                                }
                                        });
                                        this.jRes.addFunc({
                                                breakpoint: 'tablet',
                                                enter: function () {
                                                        _this.$emit('screenChange','tablet');
                                                },
                                                exit: function () {
                                                }
                                        });
                                        this.jRes.addFunc({
                                                breakpoint: 'phone',
                                                enter: function () {
                                                        _this.$emit('screenChange','phone');
                                                },
                                                exit: function () {
                                                }
                                        });
                                }
                        },
                        created:function(){
                                this.initRespond();
                        },
                        mounted:function(){
                        },
                        beforeMount:function(){

                        }
                })
        },
        name:'AmUI'
}

export default AmUI




