
import AmUI from './amui/v1.0.0/index';
// import IView from './plus/iview/index'


import './plus/iview/styles/index.less'
import './amui/styles/index.scss'


const install = function(Vue, opts = {}) {
        AmUI.install(Vue,opts);
        // IView.install(Vue,opts);
};

//自动安装
/****/
if (typeof window !== 'undefined' && window.Vue) {
        install(window.Vue);
}

export default {
        ...AmUI,
        // ...IView,
        install
}


