<template>
    
    <aside id="sidebar" :style="getStyles" class="page-sidebar sidebar-fixed" :class="[{'collapse-sidebar':collapseSidebar,'hide-sidebar':hideSidebar}]">
        <div class="sidebar-inner">
            
            <slot name="top">
    
                <div class="sidebar-panel">
                    <h5 class="sidebar-panel-title">Profile</h5>
                </div>
                <div class="user-info clearfix">
                   
                    <img :src="avatars" alt="avatar">
                    <span class="name">SuggeElson</span>
                    <div class="btn-group">
                        <button type="button" class="btn btn-default btn-xs"><i class="l-basic-gear"></i>
                        </button>
                        <button type="button" class="btn btn-default btn-xs dropdown-toggle" data-toggle="dropdown">settings <span class="caret"></span>
                        </button>
                        <ul class="dropdown-menu right" role="menu" style="margin-left: -24px;">
                            <li><a href="profile.html"><i class="fa fa-edit"></i>Edit profile</a>
                            </li>
                            <li><a href="#"><i class="fa fa-money"></i>Windraws</a>
                            </li>
                            <li><a href="#"><i class="fa fa-credit-card"></i>Deposits</a>
                            </li>
                            <li class="divider"></li>
                            <li><a href="login.html"><i class="fa fa-power-off"></i>Logout</a>
                            </li>
                        </ul>
                    </div>
                </div>
                
            </slot>
           
        
            <div class="sidebar-panel">
                <h5 class="sidebar-panel-title">{{title}}</h5>
            </div>
        
            <div class="side-nav">
    
                <ul class="nav">
                    <Menu
                            :collapseSidebar="collapseSidebar"
                            v-for="(item, i) in stateTree"
                            :key="i"
                            :index="i"
                            :data="item">
                    </Menu>

                </ul>
                    
            </div>
        
            <slot></slot>
        </div>
    </aside>
    
    
</template>


<script>
    //区域滚动条
    import '@components/amui/assets/plugin/core/slimscroll/jquery.slimscroll.min.js'
    import '@components/amui/assets/plugin/core/slimscroll/jquery.slimscroll.horizontal.min.js'


    import avatars from '@components/amui/assets/img/avatars/128.jpg'
    
    
    import Menu from './menu.vue'
    import Emitter from '../../mixins/emitter'

    const prefixCls = 'am-sidebar';
    
    
    export default {
        name:'LayoutSider',
        mixins: [ Emitter],
        components: { Menu },
        data () {
            return {
                name:'SideBar',
                stateTree: this.data,
                collapseSidebar:false,
                hideSidebar:false,
                avatars:avatars
            }
        },
        props:{
            data: {
                type: Array,
                default () {
                    return [];
                },
                toWidth:this.width
            },
            loadData: {
                type: Function
            },
            render: {
                type: Function
            },
            conflict:{//菜单交互效果,如果点击其他菜单,上一个菜单是否收起来
                type:String,
                default () {
                    return '';
                }
            },
            width:{
                type:[Number,String],
                default:210
            },
            title:{
                type:String,
                default:'功能菜单'
            }
        },
        computed:{
            getStyles:function(){
                var temp_width = 0;
                if(this.collapseSidebar==true){
                    temp_width = "60px"
                }else{
                    temp_width = this.width +"px"
                }
                return {
                    width:temp_width
                }
            }
        },
        watch:{
            data:{
                deep: true,
                handler () {
                    this.stateTree = this.data;
                }
            },
            collapseSidebar(val){
                if(!val){
                    $(".slimScrollDiv").css('overflow','hidden');
                    $(".sidebar-inner").css('overflow','hidden');
                }else{
                    $(".slimScrollDiv").css('overflow','inherit');
                    $(".sidebar-inner").css('overflow','inherit');
                }
            }
        },
        methods:{
            screenChange:function(type){
                if(type == "phone"){
                    this.hideSidebar = true;
                    this.collapseSidebar = false;
                }else if(type == "tablet"){
                    this.collapseSidebar = true;
                    this.hideSidebar = false;
                }else if(type == "laptop"){
                    this.collapseSidebar = false;
                    this.hideSidebar = false;
                }else if(type == "large"){
                    this.collapseSidebar = false;
                    this.hideSidebar = false;
                }
            },
            openMenuEvent:function(type){
                var screen = this.$amui.getBreakpoint();
                if(type == "open"){
                    if(screen == 'phone'){
                        this.hideSidebar = false;
                        this.collapseSidebar = false;
                    }else if(screen == 'tablet'){
                        this.collapseSidebar = false;
                        this.hideSidebar = false;
                    }else if(screen == 'laptop'){
                        this.collapseSidebar = false;
                    }else if(screen == 'large'){
                        this.collapseSidebar = false;
                    }
                }else{
                    if(screen == 'phone'){
                        this.hideSidebar = true;
                        this.collapseSidebar = false;
                    }else if(screen == 'tablet'){
                        this.collapseSidebar = true;
                    }else if(screen == 'laptop'){
                        this.collapseSidebar = true;
                    }else if(screen == 'large'){
                        this.collapseSidebar = true;
                    }
                }
            }
        },
        created(){
            this.stateTree = this.data;
        },
        mounted () {
            var _this = this;
            $(".sidebar-inner").slimScroll({
                height: '100%',
                
                complate:function(){
                    if(_this.collapseSidebar){
                        $(".slimScrollDiv").css('overflow','inherit');
                        $(".sidebar-inner").css('overflow','inherit');
                    }
                }
            });
            
            this.$amui.$on('screenChange',function(type){
                _this.screenChange(type);
            });
    
	    this.$amui.$on('openMenuEvent',function(type){
                _this.openMenuEvent(type);
	    });
    
            this.screenChange(this.$amui.getBreakpoint());
            
        }
    }
</script>


