// 组件库
import AmUI from './AmUI';
import App from './App'

import Layout from './layout'
import LayoutSider from './sidebar'

import Btn from './Btn'
import Alert from './alert'

Array.prototype.contains = function(element) {
        for (var i = 0; i < this.length; i++) {
                if (this[i] == element) {
                        return true;
                }
        }
        return false;
}

//引入css文件


const components = {
        AmUI,
        aBtn:Btn,
        aAtn:Alert,
        aApp:App,
        LayoutHeader:Layout.header,
        HeaderMenu:Layout.header.HeaderMenu,
        headerBtn:Layout.header.HeaderButton,
        LayoutRight:Layout.right,
        LayoutContent:Layout.content,
        LayoutFooter:Layout.footer,
        LayoutSider,
        MenuItem:LayoutSider.MenuItem,
        MenuItemto:LayoutSider.MenuItemto,
        aSidePanel:LayoutSider.sidePanoel
}


const install = function(Vue, opts = {}) {
        if (install.installed) return;
        Vue.use(AmUI);

        Object.keys(components).forEach(key => {
                Vue.component(key, components[key]);
        });
};

if (typeof window !== 'undefined' && window.Vue) {
        //install(window.Vue);
}

const API = {
        install,
        ...components
};

export default API;



